body {
  margin: 0;
  padding: 0;
  font-family: "Lato", sans-serif;
  /* font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif; */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  width: 100%;
  margin: auto;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 5s linear;
          animation: App-logo-spin infinite 5s linear;
  height: 10vmin;
}

  .App-logo:focus,
  .App-logo:hover {
      -webkit-animation: App-logo-spin infinite 1.5s linear;
              animation: App-logo-spin infinite 1.5s linear;
  }

.App-header {
  background-color: #282c34;
  min-height: 15vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

#anim { 
  max-width: 800px; 
  margin: 0 auto; 
}

@-webkit-keyframes App-logo-spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  50% {
    -webkit-transform: rotate(-180deg);
            transform: rotate(-180deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  50% {
    -webkit-transform: rotate(-180deg);
            transform: rotate(-180deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
.Button {
    position: relative;
    border: none;
    outline: none;
    cursor: pointer;
    border-radius: 100px;
    font: inherit;
    padding: 8px !important;
    margin: 5px !important;
    font-weight: bold;
    text-align: center;
    /* background-color: transparent; */
    /* color: white; */
    /* font-size: 16px; */
}
  
.Btn > * {
    transition: all 0.2s;
}

.Submit:focus,
.Submit:hover {
    -webkit-transform: translateY(-3px);
            transform: translateY(-3px);
    -webkit-animation: standOut 1.5s infinite;
            animation: standOut 1.5s infinite;
}

.Delete:focus,
.Delete:hover {
    -webkit-transform: translateY(-3px);
            transform: translateY(-3px);
    -webkit-animation: pulsate 1.5s infinite;
            animation: pulsate 1.5s infinite;
}

.Edit:focus,
.Edit:hover {
    -webkit-transform: translateY(-3px);
            transform: translateY(-3px);
    -webkit-animation: pulsate 1.5s infinite;
            animation: pulsate 1.5s infinite;
}

@-webkit-keyframes pulsate {
    0% {
        -webkit-transform: scale(1);
                transform: scale(1);
        box-shadow: none;
    }

    50% {
        -webkit-transform: scale(1.20);
                transform: scale(1.20);
    }

    100% {
        -webkit-transform: scale(1);
                transform: scale(1);
        box-shadow: none;
    }
}

@keyframes pulsate {
    0% {
        -webkit-transform: scale(1);
                transform: scale(1);
        box-shadow: none;
    }

    50% {
        -webkit-transform: scale(1.20);
                transform: scale(1.20);
    }

    100% {
        -webkit-transform: scale(1);
                transform: scale(1);
        box-shadow: none;
    }
}

@-webkit-keyframes standOut {
    0% {
        -webkit-transform: scale(1);
                transform: scale(1);
        box-shadow: none;
    }

    100% {
        -webkit-transform: scale(1.30);
                transform: scale(1.30);
    }
}

@keyframes standOut {
    0% {
        -webkit-transform: scale(1);
                transform: scale(1);
        box-shadow: none;
    }

    100% {
        -webkit-transform: scale(1.30);
                transform: scale(1.30);
    }
}
.max__size {
    margin: 0px auto;
    width: 80%;
    text-align: center;
}

.title__font { 
    font-family: 'Lobster', cursive;
    letter-spacing: 2px;
    transition: all 0.2s;
}

    .title__font:focus,
    .title__font:hover {
        -webkit-transform: translateY(-3px);
                transform: translateY(-3px);
        -webkit-animation: standOut 1.5s infinite;
                animation: standOut 1.5s infinite;
    }


.title__font--list {
    font-family: 'Lobster', cursive;
    letter-spacing: 2px;
    transition: all 0.2s;
    /* font-family: 'Anton', sans-serif; */
}
    .title__font--list span:focus,
    .title__font--list span:hover {
        text-shadow: 0 0 3px #FF0000, 0 0 5px #0000FF;
    }

.smaller {
     font-size: 22px;
}

.body__font {
    font-family: 'Lato', sans-serif;
    font-weight: 500;
    font-size: 18px;
    letter-spacing: 1px;
}

input[type=text] {
    text-align: center;
    width: 50%;
    padding: 6px 10px;
    margin: 1px 0;
    box-sizing: border-box;
    font-size: 17px;
    outline: none;
    /* border-radius: 100px; */
}

textarea[type=text] {
    text-align: center;
    width: 50%;
    box-sizing: border-box;
    padding: 6px 10px;
    margin: 1px 0;
    font-size: 17px;
    outline: none;
    /* border-radius: 100px; */
}

@-webkit-keyframes standOut {
    0% {
        -webkit-transform: scale(1);
                transform: scale(1);
        box-shadow: none;
    }

    100% {
        -webkit-transform: scale(1.10);
                transform: scale(1.10);
    }
}

@keyframes standOut {
    0% {
        -webkit-transform: scale(1);
                transform: scale(1);
        box-shadow: none;
    }

    100% {
        -webkit-transform: scale(1.10);
                transform: scale(1.10);
    }
}
