.max__size {
    margin: 0px auto;
    width: 80%;
    text-align: center;
}

.title__font { 
    font-family: 'Lobster', cursive;
    letter-spacing: 2px;
    transition: all 0.2s;
}

    .title__font:focus,
    .title__font:hover {
        transform: translateY(-3px);
        animation: standOut 1.5s infinite;
    }


.title__font--list {
    font-family: 'Lobster', cursive;
    letter-spacing: 2px;
    transition: all 0.2s;
    /* font-family: 'Anton', sans-serif; */
}
    .title__font--list span:focus,
    .title__font--list span:hover {
        text-shadow: 0 0 3px #FF0000, 0 0 5px #0000FF;
    }

.smaller {
     font-size: 22px;
}

.body__font {
    font-family: 'Lato', sans-serif;
    font-weight: 500;
    font-size: 18px;
    letter-spacing: 1px;
}

input[type=text] {
    text-align: center;
    width: 50%;
    padding: 6px 10px;
    margin: 1px 0;
    box-sizing: border-box;
    font-size: 17px;
    outline: none;
    /* border-radius: 100px; */
}

textarea[type=text] {
    text-align: center;
    width: 50%;
    box-sizing: border-box;
    padding: 6px 10px;
    margin: 1px 0;
    font-size: 17px;
    outline: none;
    /* border-radius: 100px; */
}

@keyframes standOut {
    0% {
        transform: scale(1);
        box-shadow: none;
    }

    100% {
        transform: scale(1.10);
    }
}